@import "../../util/scss/mixins";
.aboutus {
  &__header {
    height: 70vh;
    position: relative;
    background-image: linear-gradient(
        45deg,
        rgba(3, 169, 244, 0.6),
        rgba(33, 150, 243, 0.7)
      ),
      url("../../assets/images/desert.jpg");

    background-size: cover;
    background-position: top;
  }
  & &__title {
    color: white;
    @include centerElement;
    font-weight: bold;
  }
  &__intro {
    position: relative;
    display: flex;
    margin-top: -4rem;
    &-content {
      width: 50%;
    }
  }
  & &__sub-title {
    color: var(--blue);
    margin-bottom: 4rem;
    &--mission {
      text-align: center;
    }
  }
  &__descrp {
    font-size: 2rem;
    &--story {
      width: 35%;
    }
  }
  &__video {
    width: 60%;
    max-width: 70rem;
    height: 50rem;
  }
  &__story {
    &-content {
      display: flex;
      justify-content: space-evenly;
    }
  }
  &__curve {
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
  }
}
